define('frontend-cp/components/ko-checkbox/component', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Component.extend({
    disabled: false,
    large: false,
    checked: false,
    ariaLive: 'assertive',
    tabindex: 0,
    label: '',
    classNames: ['ko-checkbox'],

    // https://github.com/terrawheat/ember-cli-keyboard-actions/issues/1

    keyDown: function keyDown(e) {
      if (e.keyCode === 32) {
        return false;
      }
    },

    keyUp: function keyUp(e) {
      if (e.keyCode === 32) {
        this.send('toggleCheckbox');
      }
      return false;
    },

    actions: {
      toggleCheckbox: function toggleCheckbox() {
        if (!this.disabled) {
          if (this.get('onCheck')) {
            this.sendAction('onCheck', !this.get('checked'));
          } else {
            this.toggleProperty('checked');
          }
        }
      }
    }

  });

});